<template>
	<div class="v-plan-view" v-loading="loading">
		<!--  &  -->
		<div class="m-plan-navigation">
			<el-button class="u-goback" size="medium" icon="el-icon-arrow-left" @click="goBack" plain></el-button>
		</div>
		<!--  -->
		<WikiPanel class="m-plan-content" :wiki-post="plan">
			<!--  -->
			<template slot="head-title">
				<i class="el-icon-tickets"></i>
				<span class="u-plan-title"> {{ plan.title }} </span>
			</template>
			<!--  &  &  -->
			<template slot="head-actions">
				<Fav post-type="item_plan" :post-id="plan.id" :post-title="plan && plan.title" />
			</template>
			<!--  -->
			<template slot="body">
				<!--  -->
				<div class="m-description m-border">
					<div class="u-title">
						<span></span>
						<div class="u-button" v-if="isAuthor">
							<el-button type="primary" icon="el-icon-edit" size="mini" plain @click="editPlan(plan.id)"></el-button>
							<el-button type="danger" icon="el-icon-delete" size="mini" plain @click="deletePlan(plan.id)"></el-button>
						</div>
					</div>
					<div class="u-desc">{{ plan.description || " " }}</div>
					<span class="u-user">
						<img class="u-avatar" :src="showAvatar(getUserInfo(plan, 'user_avatar')) || default_avatar" :alt="getUserInfo(plan, 'display_name')" />
						<a class="u-name" :href="authorLink(plan.user_id)">{{ getUserInfo(plan, "display_name") || "" }}</a>
						<span class="u-time"> <i class="el-icon-time"></i>{{ date_format(plan.updated) }}</span>
					</span>
				</div>
				<!--  -->
				<div class="m-plan-item" v-if="plan.type == '1'">
					<div v-for="(item, index) in plan.relation" :key="index">
						<div class="m-border" v-if="item.data && item.data.length">
							<div class="u-title" v-if="item.title">{{ item.title }}</div>
							<div class="u-content">
								<router-link class="u-item" v-for="(el, key) in item.data" :key="key" :to="{ name: 'view', params: { item_id: el.id } }">
									<span class="u-img">
										<itemIcon :item="el" />
										<span class="u-count">{{ el.count }}</span>
									</span>
									<span class="u-name" :class="`quality-${el.Quality}`"> {{ el.Name }}</span>
								</router-link>
							</div>
						</div>
					</div>
				</div>
				<!--  -->
				<div class="m-border m-plan-equips" v-else-if="plan.type == '2'">
					<Equip :data="plan" />
					<div class="u-content">
						<div class="u-list" v-for="(list, index) in equipList" :key="index">
							<div class="u-item" v-for="(item, key) in list" :key="key">
								<span class="u-title"> {{ item.label }}</span>
								<div v-if="item.list.length">
									<itemIcon class="u-equip" v-for="(eq, i) in item.list" :key="i" :has_title="true" :item="eq" />
								</div>
								<div v-else class="u-equip-null">-  -</div>
							</div>
						</div>
					</div>
				</div>
			</template>
		</WikiPanel>

		<!--  -->
		<div>
			<el-divider content-position="left"></el-divider>
			<Comment :id="plan_id" category="plan_view" />
		</div>
	</div>
</template>
<script>
import { getItemPlanID, delItemPlans, searchItemsID } from "../service/item_plan.js";
import itemIcon from "@/components/ItemIcon.vue";
import Equip from "@/components/Equip.vue";
import WikiPanel from "@jx3box/jx3box-common-ui/src/wiki/WikiPanel";
import Comment from "@jx3box/jx3box-comment-ui/src/Comment.vue";
import { iconLink } from "@jx3box/jx3box-common/js/utils";
import { __Links, default_avatar } from "@jx3box/jx3box-common/data/jx3box.json";
import { showAvatar, authorLink, ts2str } from "@jx3box/jx3box-common/js/utils";
import User from "@jx3box/jx3box-common/js/user";

export default {
	name: "PlanDetail",
	props: [],
	components: { Comment, WikiPanel, itemIcon, Equip },
	data: function () {
		return {
			loading: false,

			isAuthor: false,
			plan: "",
			date_format: ts2str,
			default_avatar,
			equipList: [
				[
					{ title: "melee_weapon", label: "", AucGenre: 1, list: [] },
					{ title: "range_weapon", label: "", AucGenre: 2, list: [] },
				],
				[
					{ title: "helm", label: "", AucGenre: 3, AucSubType: 2, list: [] },
					{ title: "chest", label: "", AucGenre: 3, AucSubType: 1, list: [] },
					{ title: "waist", label: "", AucGenre: 3, AucSubType: 3, list: [] },
				],
				[
					{ title: "bangle", label: "", AucGenre: 3, AucSubType: 6, list: [] },
					{ title: "pants", label: "", AucGenre: 3, AucSubType: 4, list: [] },
					{ title: "boots", label: "", AucGenre: 3, AucSubType: 5, list: [] },
				],
				[
					{ title: "amulet", label: "", AucGenre: 4, AucSubType: 1, list: [] },
					{ title: "pendant", label: "", AucGenre: 4, AucSubType: 3, list: [] },
					{ title: "ring_1", label: "", AucGenre: 4, AucSubType: 2, list: [] },
					{ title: "ring_2", label: "", AucGenre: 4, AucSubType: 2, list: [] },
				],
			],
		};
	},
	computed: {
		plan_id() {
			return this.$route.params.plan_id;
		},
		type() {
			return 1;
		},
	},

	methods: {
		// 
		getItemData() {
			getItemPlanID(this.plan_id)
				.then((res) => {
					// this.plan = res;
					this.converted(res);
					if (res.type == 2) this.toEquipList(res.relation);
					this.isAuthorUser(res.user_id);
				})
				.finally(() => {
					this.loading = false;
				});
		},
		// 
		isAuthorUser(user_id) {
			let _user = User.getInfo();
			if (_user.uid == user_id) this.isAuthor = true;
		},
		// 
		goBack() {
			history.length ? this.$router.go(-1) : this.$router.push({ name: "plan_list" });
		},
		// ,
		converted(data) {
			if (data.type == 1) {
				data.relation = data.relation.map((item) => {
					item.data = item.data.map((el) => {
						if (typeof el == "string") el = { id: el, count: 1 };
						if (typeof el == "object") el = { id: el.id, count: el.count };
						return el;
					});
					return item;
				});

				// 
				let _arr = [];
				data.relation.forEach((list) => {
					list.data.forEach((el) => {
						_arr.push(el.id);
					});
				});
				searchItemsID({ ids: _arr, limit: _arr.length }).then((res) => {
					let items = res.data;

					data.relation = data.relation.map((item) => {
						item.data = item.data.map((el) => {
							let a = items.filter((k) => {
								if (k.id == el.id) return { ...k, ...el };
							});
							el = { ...el, ...a[0] };
							return el;
						});
						return item;
					});
					this.plan = data;
				});
			} else {
				data.relation = this.equipItem(data.relation);
				let _arr = [];
				for (const key in data.relation) {
					_arr.push(...data.relation[key]);
				}
				searchItemsID({ ids: _arr, limit: _arr.length }).then((res) => {
					let items = res.data;
					for (const key in data.relation) {
						data.relation[key] = data.relation[key].map((el) => {
							el = items.filter((k) => {
								if (el == k.id) return k;
							});
							return el[0];
						});
					}
					this.plan = data;
					this.equipList.map((list) => {
						list.map((item) => {
							item.list = item.list.map((el) => {
								el = items.filter((k) => {
									if (k.id == el) return k;
								});
								return el[0];
							});
							return item;
						});
						return list;
					});
				});
			}
		},
		// objectstring
		equipItem(data) {
			for (const key in data) {
				data[key] = data[key].map((item) => {
					if (typeof item == "object") item = item.id;
					return item;
				});
			}
			return data;
		},
		// 
		toEquipList(_obj) {
			this.equipList.map((list) => {
				list.map((el) => {
					if (_obj.hasOwnProperty(el.title)) {
						el.list = _obj[el.title];
					}
				});
			});
		},
		// 
		toEquip() {
			let obj = {};
			this.equipList.forEach((list) => {
				list.forEach((el) => {
					obj[el.title] = el.list;
				});
			});
			obj = this.equipItem(obj);
			this.plan.relation = obj;
		},

		// 
		publish_url(val) {
			return `${__Links.dashboard.publish}#/${val}`;
		},
		// 
		editPlan(plan_id) {
			location.href = this.publish_url(`item_plan/${plan_id}`);
		},
		// 
		deletePlan(plan_id) {
			this.$confirm("", "", {
				confirmButtonText: "",
				cancelButtonText: "",
				type: "warning",
			}).then(() => {
				delItemPlans(plan_id)
					.then((res) => {
						this.$message.success(res.message);
						this.$router.push({ name: "plan_list" });
					})
					.catch((e) => {
						this.$message.error(e.message);
					});
			});
		},

		iconLink,
		showAvatar,
		authorLink,
		getUserInfo: function (plan, key) {
			return plan?.user_info?.[key];
		},
	},
	created: function () {
		this.getItemData();
	},
};
</script>
<style lang="less" scoped>
@import "../assets/css/views/plan_detail.less";
</style>
<style lang="less">
.m-panel-head{
    .z(1);
}
.v-plan-view .m-item-icon {
	.u-item-icon {
		.size(48px);
		max-height: 48px;
		.z(0);
	}
	.u-border {
		.z(1);
	}
	.u-border-quest {
		.z(2);
	}
}

.m-item-icon-popup {
	padding: 0;
}
</style>
